.design-system-modal-component-wrapper {
    background: #fff;
    color: #222;
    height: 100%;
    left: 0;
    overflow: auto;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999999;
    opacity: 0.8;

    .modal-component-overlay {
        align-items: center;
        background: transparent;
        display: flex;
        height: 100%;
        justify-content: center;
        width: 100%;
    }
    .modal-content {
        border-radius: 8px;
        padding: 24px;
        border: 1px solid #E2EBF2;
    }
}