$box-shadow: 0px 0px 12px 0px rgba(76, 93, 158, 0.08);

.skyplus-accordion {
  border-radius: rem(8px);
  box-shadow: $box-shadow;
  background: var(--white);
  margin-bottom: rem(12px);

  &__header {
    cursor: pointer;
    padding: rem(12px) rem(16px);

    &--active {
      background: var(--secondary-light);
      border-radius: rem(8px) rem(8px) 0 0;
    }

    &-right {
      width: 100%;
    }

    &-left {
      background: var(--secondary-main);
      width: rem(4px);
      border-radius: rem(4px);
      margin-right: rem(12px);

      &--active {
        background: var(--primary-main);
      }

      &--added {
        background: var(--accent-main);
      }
    }

    &-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: rem(4px);
    }

    &-name {
      color: var(--text-primary);
      font-size: rem(14px);
    }

    @include media('>=tablet') {
      padding: rem(16px) rem(24px) rem(16px) rem(24px);

      &-name {
        font-size: rem(16px);
      }
    }
  }

  &__head-root {
    display: flex;
  }

  &__subtitle {
    font-size: rem(12px);
    color: var(--text-tertiary);

    @include media('>=tablet') {
      font-size: rem(14px);
    }
  }

  &__footer {
    margin-top: rem(12px);
    border: rem(1.5px) dotted var(--secondary-main);
    padding: rem(12px);
    border-radius: rem(8px);

    &-text {
      font-size: rem(12px);
      color: var(--text-primary);
    }
  }

  &__body {
    padding: rem(16px) rem(12px);

    @include media('>=tablet') {
      padding: rem(24px) rem(24px) rem(16px);
    }
  }

  &__icon {
    transition: all 0.6s;
    color: var(--text-primary);
    font-weight: bold;
  }
}
