.flight-journey-tab-container {
  display: flex;
  flex-direction: row;
  overflow: auto hidden;

  &::-webkit-scrollbar {
    background-color: transparent;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &__outer {
    padding: rem(4px);
    min-width: rem(160px);

    @include media('>=tablet') {
      min-width: rem(500px);
    }

    flex: 1;
    border: var(--border-secondary);

    &:nth-child(1) {
      border-top-left-radius: rem(24px);
      border-bottom-left-radius: rem(24px);
    }

    &:nth-last-child(1) {
      border-top-right-radius: rem(24px);
      border-bottom-right-radius: rem(24px);
    }

    &:not(:nth-child(1)) {
      border-left: 0;
    }

    &:not(:nth-last-child(1)) {
      border-right: 0;
    }
  }

  &__leg {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: rem(8px) rem(16px);
    cursor: pointer;

    span {
      font-size: rem(16px);
      display: inline-block;
      font-weight: 500;
      color: get-color(text-tertiary);

      &:nth-child(1) {
        display: flex;
        align-items: center;
        justify-content: center;

        &::after {
          content: '';
          border: rem(1px) dotted;
          width: rem(24px);

          @include media('>=tablet') {
            width: rem(48px);
          }

          display: inline-block;
          margin: 0 rem(10px);

          @include media('>=tablet') {
            margin: 0 rem(20px);
          }

          border-bottom: 0;
          border-left: 0;
          border-right: 0;
        }
      }
    }
  }

  &__leg.active {
    background-color: get-color(primary-main);
    border-radius: rem(24px);

    span {
      color: get-color(white);
    }
  }
}
