.skyplus-checkbox-v2 {
  input {
    appearance: none;
    outline: rem(2) solid var(--primary-main);
    height: rem(16);
    width: rem(16);
    border-radius: rem(4);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    &::before {
      visibility: hidden;
      content: '✓';
      font-size: rem(16);
      color: var(--white);
    }

    &:checked {
      background-color: var(--primary-main) !important;

      &::before {
        visibility: visible !important;
      }
    }
  }
}
