.design-system-input-field {
    input {
        border-radius: 8px;
        border: 1px solid  #E2EBF2;
        background: #fff;
        justify-content: space-between;
        padding: 12px 0 12px 8px;
        width: 100%;
        color: #25304B;
        font-size: 14px;
        font-weight: 300;
    }
    input::placeholder {
        color: #7A85A0;
        ;
    }
    .error-msg {
        font-size: 12px;
        color: red;
        margin-left: 8px;
    }
    .input-field-cross-btn {
        right: 15px;
        top: 13px;
        color: #000099;

    }
}