.skyplus-chip {
  font-size: rem(12px);
  line-height: 1.5;
  font-weight: 500;
  cursor: pointer;
  display: inline-block;
  border-radius: rem(32px);
  border: 1px solid transparent;

  [class*='chip-outlined'] {
    border: 1px solid currentcolor;
    border-radius: rem(32px);

    span {
      color: get-color(text-primary);
    }
  }

  [class*='chip-filled'] {
    background-color: currentcolor;

    span {
      color: get-color(white);
    }
  }

  [class*='text-filled'] {
    background-color: currentcolor;
    border: 1px solid get-color();

    span {
      color: get-color(text-primary);
    }
  }

  [class*='col-system-success'] {
    color: get-color(system-success);
  }

  [class*='col-primary-main'] {
    color: get-color(primary-main);
  }

  [class*='col-secondary-main'] {
    color: get-color(secondary-main);
  }

  [class*='col-secondary-light'] {
    color: get-color(secondary-light);
  }

  [class*='col-white'] {
    color: get-color(white);
  }

  [class*='size-xs'] {
    padding: rem(2px) rem(6px);
  }

  [class*='size-sm'] {
    padding: rem(4px) rem(9px);
    border-radius: rem(32px);
  }

  [class*='size-md'] {
    padding: rem(8px) rem(12px);
    border-radius: rem(32px);
  }

  [class*='text-outlined'] {
    border-radius: rem(32px);
    border: 1px solid currentcolor;
  }

  [class*='bg-secondary-main'] {
    border-radius: rem(32px);
    border: 1px solid get-color(secondary-main);
  }

  [class*='bg-system-information'] {
    border-radius: rem(32px);
    border: 1px solid get-color(system-information);
  }

  [class*='txtcol-system-information'] {
    span {
      color: get-color(system-information);
    }
  }

  [class*='txtcol-system-success'] {
    span {
      color: get-color(system-success);
    }
  }

  [class*='bg-system-success'] {
    border-radius: rem(32px);
    border: 1px solid get-color(system-success);
  }

  [class*='txtcol-text-primary'] {
    span {
      color: get-color(text-primary);
    }
  }

  [class*='txtcol-text-secondary'] {
    span {
      color: get-color(text-secondary);
    }
  }

  .text-text-primary {
    color: var(--text-primary);
  }

  .text-primary-main {
    color: var(--primary-main);
  }
}
