.skyplus-offcanvas {
  &__overlay {
    position: fixed;
    inset: 0;
    visibility: visible;
    opacity: 1;
    transition: all 0.25s ease-in;
    background-color: rgb(0 0 0 / 50%);
    z-index: 99;
  }

  &__contents {
    padding: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 100%;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: var(--secondary-white);
    transition: all 0.25s ease-in-out;
    transform: translate(-100%, 0);
    display: flex;
    flex-direction: column;
    z-index: 100;

    @include media('>=tablet') {
      width: rem(480px);
    }

    &--title {
      font-size: rem(24px);
    }

    &--header {
      height: rem(50px);
      display: flex;
      align-items: center;
      background-color: var(--secondary-white);
      justify-content: flex-start;
      box-shadow: 0 0 12px 0 #4c5d9e14;

      &::after {
        margin-left: rem(20px);
        font-family: $font-family-indigo;
        content: '\e949' !important;
        color: var(--action-active);
        cursor: pointer;
      }
    }

    &--body {
      padding: 0 rem(20px) rem(80px);
      overflow: auto;
      background: linear-gradient(
        180deg,
        rgb(254 254 254 / 0%) 0%,
        rgb(255 255 255 / 95.9%) 8.73%,
        #f7f9fe 17.51%,
        #ecf7fc 81.97%
      );
      flex: 1;
      overflow-x: hidden;
    }

    &--footer {
      display: flex;
      align-items: center;
      justify-content: center;
      height: rem(80px);
      background-color: var(--secondary-white);
      border-top-left-radius: rem(16px);
      border-top-right-radius: rem(16px);
      bottom: 0;
      position: absolute;
      width: 100%;
    }
  }

  &.is-hidden {
    .skyplus-offcanvas {
      &__overlay {
        opacity: 0;
        visibility: hidden;
      }

      &__contents {
        transform: translate(0, 0);
      }
    }
  }
}
