.radio-label {
  color: #303030;
  font-size: 14px;
  font-weight: 400;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  line-height: 20px;
  padding: 10px 0;
}

.radio-label.checked {
  color: get-color(primary-main);
}

.radio-input {
  margin: 0;
  visibility: hidden;
}

.radio-input:checked + span {
  border: 2px solid get-color(sel-con-selected-background);
}

.radio-input:checked + span::after {
  opacity: 1;
}

.custom-radio {
  left: -8px;
  cursor: pointer;
  width: 20px;
  height: 20px;
  border: 2px solid get-color(sel-con-selected-background);
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

/* for inner filled circle */
.custom-radio::after {
  content: '';
  width: 12px;
  height: 12px;
  background: get-color(sel-con-selected-background);
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.2s;
}
